@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lexend+Deca&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{margin:0;padding:0;font-family:"Inter","Lexend Deca",sans-serif;font-size:15px;color:#fff}.container{display:flex;justify-content:center;align-items:center;width:100vw;height:100vh;background-color:#090b1a}.card{width:75%;display:flex;background-color:#1b1938;border-radius:10px}.card__img{background-color:#aa5cdb;border-radius:0 10px 10px 0}.card__img>img{opacity:0.5;border-radius:0 10px 10px 0}.card__body{display:flex;flex-direction:column;justify-content:space-evenly;padding:30px}.card__footer{display:flex;justify-content:space-between;padding-right:150px}h3{font-size:2.3em}h3>span{font-size:1em;color:#aa5cdb}.card__items{display:flex;flex-direction:column}@media (min-width: 320px) and (max-width: 992px){.card{display:flex;flex-direction:column-reverse}.card__img{border-radius:10px 10px 0 0}.card__img>img{width:100%;src:url(/stats-preview-card-component/static/media/image-header-mobile.399f71e7.jpg);border-radius:10px 10px 0 0}.card__body{align-items:center;padding:30px}.card__footer{flex-direction:column;padding-right:0px}.card__text,.card__title{text-align:center}.card__items{align-items:center}}

